import React, { useState, useEffect } from "react";
import { useNavigate, Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {
  register,
  login,
  selectAuthLoading,
  selectAuthError,
  clearError,
} from "../../store/slices/authSlice";
import { useToast } from "../../contexts/ToastContext";

export default function RegisterFormRedux() {
  const [form, setForm] = useState({
    name: "",
    email: "",
    password: "",
    confirmPassword: "",
  });

  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { addToast } = useToast();

  const loading = useSelector(selectAuthLoading);
  const error = useSelector(selectAuthError);

  // Clear error when component unmounts
  useEffect(() => {
    return () => {
      dispatch(clearError());
    };
  }, [dispatch]);

  // Show error toast
  useEffect(() => {
    if (error) {
      addToast(error, "error");
    }
  }, [error, addToast]);

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    // Validation
    if (!form.name || !form.email || !form.password || !form.confirmPassword) {
      addToast("Vui lng in y  thng tin", "warning");
      return;
    }

    if (form.password.length < 6) {
      addToast("Mt khu phi c t nht 6 k t", "warning");
      return;
    }

    if (form.password !== form.confirmPassword) {
      addToast("Mt khu xc nhn khng khp", "error");
      return;
    }

    try {
      // Register
      const registerAction = await dispatch(
        register({
          name: form.name,
          email: form.email,
          password: form.password,
        })
      );

      if (register.fulfilled.match(registerAction)) {
        addToast("ng k thnh cng!", "success");

        // Auto login after register
        const loginAction = await dispatch(
          login({
            email: form.email,
            password: form.password,
          })
        );

        if (login.fulfilled.match(loginAction)) {
          addToast(" ng nhp!", "success");
          navigate("/profile");
        } else {
          // Login failed, redirect to login page
          navigate("/login");
        }
      } else {
        // Register failed - error already shown
        console.error("Register failed:", registerAction.payload);
      }
    } catch (err) {
      console.error("Unexpected error:", err);
      addToast(" xy ra li khng mong mun", "error");
    }
  };

  return (
    <div className="auth-wrap">
      <div className="auth-card">
        <h2 className="auth-title">ng k</h2>
        <p className="auth-sub">To ti khon mi  bt u</p>

        <form onSubmit={handleSubmit}>
          <div className="form-group">
            <label>H v tn</label>
            <input
              type="text"
              name="name"
              placeholder="Nguyn Vn A"
              value={form.name}
              onChange={handleChange}
              required
              disabled={loading}
            />
          </div>

          <div className="form-group">
            <label>Email</label>
            <input
              type="email"
              name="email"
              placeholder="Email"
              value={form.email}
              onChange={handleChange}
              required
              disabled={loading}
            />
          </div>

          <div className="form-group">
            <label>Mt khu</label>
            <input
              type="password"
              name="password"
              placeholder="Mt khu (ti thiu 6 k t)"
              value={form.password}
              onChange={handleChange}
              required
              minLength={6}
              disabled={loading}
            />
          </div>

          <div className="form-group">
            <label>Xc nhn mt khu</label>
            <input
              type="password"
              name="confirmPassword"
              placeholder="Nhp li mt khu"
              value={form.confirmPassword}
              onChange={handleChange}
              required
              disabled={loading}
            />
            {form.confirmPassword && form.password !== form.confirmPassword && (
              <span style={{ fontSize: 12, color: "#ef4444", marginTop: 4 }}>
                Mt khu khng khp
              </span>
            )}
          </div>

          <div className="center">
            <button className="btn" type="submit" disabled={loading}>
              {loading ? "ang x l..." : "ng k"}
            </button>
          </div>
        </form>

        <div style={{ textAlign: "center", marginTop: 12 }}>
          <span style={{ fontSize: 13, color: "#6b7280" }}>
             c ti khon?{" "}
          </span>
          <Link to="/login" style={{ fontSize: 13, color: "#4f46e5" }}>
            ng nhp
          </Link>
        </div>
      </div>
    </div>
  );
}

