import React, { useState } from "react";
import api from "../services/api";
import { useNavigate } from "react-router-dom";
import { useToast } from "../contexts/ToastContext";

function AddUser() {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [role, setRole] = useState("user");
  const navigate = useNavigate();
  const { addToast } = useToast();

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!name || !email || !password) {
      addToast('Vui lng in  thng tin', 'warning');
      return;
    }
    if (password.length < 6) {
      addToast('Mt khu phi c t nht 6 k t', 'warning');
      return;
    }

    api.post("/users", { name, email, password, role })
      .then((res) => {
        const roleText = role === "admin" ? "Admin" : role === "moderator" ? "Moderator" : "User";
        addToast(` To user "${name}" (${roleText}) thnh cng!`, 'success');
        // i mt cht  user thy thng bo trc khi redirect
        setTimeout(() => {
          navigate("/admin");
        }, 1000);
      })
      .catch(err => {
        console.error(err);
        addToast(err.response?.data?.message || ' Li khi to user', 'error');
      });
  };

  return (
    <div className="auth-wrap">
      <div className="auth-card">
        <h2 className="auth-title">Thm ngi dng mi</h2>

        <form onSubmit={handleSubmit}>
          <div className="form-group">
            <label>Tn</label>
            <input value={name} onChange={(e) => setName(e.target.value)} required />
          </div>

          <div className="form-group">
            <label>Email</label>
            <input value={email} onChange={(e) => setEmail(e.target.value)} required />
          </div>

          <div className="form-group">
            <label>Mt khu</label>
            <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} required />
          </div>

          <div className="form-group">
            <label>Quyn</label>
            <select value={role} onChange={(e) => setRole(e.target.value)}>
              <option value="user">User</option>
              <option value="moderator">Moderator</option>
              <option value="admin">Admin</option>
            </select>
          </div>

          <div className="center">
            <button className="btn" type="submit">Thm</button>
          </div>
        </form>
      </div>
    </div>
  );
}

export default AddUser;
