import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import api from "../../services/api";
import { useToast } from "../../contexts/ToastContext";
import { useDispatch } from "react-redux";
import { logout as logoutThunk } from "../../store/slices/authSlice";
import AvatarUpload from "./AvatarUpload";
import "../../App.css";

function ProfilePage() {
  const [user, setUser] = useState({ name: "", email: "" });
  const [originalUser, setOriginalUser] = useState({ name: "", email: "" }); // Lu gi tr ban u
  const [loading, setLoading] = useState(true);
  const [message, setMessage] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [confirmNewPassword, setConfirmNewPassword] = useState("");

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { addToast } = useToast();

  //  Ly thng tin ngi dng hin ti
  const fetchProfile = async () => {
    try {
      const res = await api.get(`/profile`);
      const userData = res.data.user || res.data;
      setUser(userData);
      setOriginalUser({ name: userData.name, email: userData.email }); // Lu gi tr ban u
      setLoading(false);
    } catch (err) {
      console.error(" Li khi ti thng tin c nhn:", err);
      setMessage(" Khng th ti thng tin c nhn!");
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchProfile();
  }, []);

  //  Cp nht thng tin c nhn
  const handleUpdate = async (e) => {
    e.preventDefault();

    // Kim tra mt khu mi nu c
    if (newPassword) {
      if (newPassword.length < 6) {
        addToast("Mt khu mi phi c t nht 6 k t", "warning");
        return;
      }
      if (newPassword !== confirmNewPassword) {
        addToast("Mt khu xc nhn khng khp", "warning");
        return;
      }
    }

    // Kim tra xem c thay i g khng
    const nameChanged = user.name !== originalUser.name;
    const emailChanged = user.email !== originalUser.email;
    const passwordChanged = newPassword && newPassword.trim() !== "";
    
    const hasChanges = nameChanged || emailChanged || passwordChanged;

    if (!hasChanges) {
      addToast("Khng c thay i no  lu", "info");
      return;
    }

    try {
      const payload = { name: user.name, email: user.email };
      if (newPassword) payload.password = newPassword;
      await api.put("/profile", payload);

      // Ch hin thng bo khi c thay i thc s
      const changedFields = [];
      if (nameChanged) changedFields.push("tn");
      if (emailChanged) changedFields.push("email");
      if (passwordChanged) changedFields.push("mt khu");
      
      const changeMessage = changedFields.length > 0 
        ? `Cp nht ${changedFields.join(", ")} thnh cng`
        : "Cp nht thng tin thnh cng";
      
      addToast(changeMessage, "success");
      setNewPassword("");
      setConfirmNewPassword("");
      fetchProfile(); // Reload  cp nht originalUser
    } catch (err) {
      console.error(" Li khi cp nht:", err);
      addToast(err.response?.data?.message || "Cp nht tht bi", "error");
    }
  };

  //  Xa ti khon (ngi dng t xa)
  const handleDeleteAccount = async () => {
    const ok = window.confirm(
      "Bn c chc mun xa ti khon ca mnh? Hnh ng ny khng th hon tc."
    );
    if (!ok) return;

    try {
      await api.delete("/profile");
      addToast("Ti khon  c xa", "success");
      dispatch(logoutThunk());
      localStorage.removeItem("userId");
      navigate("/");
    } catch (err) {
      console.error(" Li khi xa ti khon:", err);
      addToast(err.response?.data?.message || "Xa tht bi", "error");
    }
  };

  //  ng xut ti khon
  const handleLogout = () => {
    dispatch(logoutThunk());
    addToast("ng xut thnh cng", "info");
    navigate("/login");
  };

  if (loading) return <p className="loading"> ang ti thng tin...</p>;

  return (
    <div className="auth-wrap">
      <div className="auth-card">
        <div
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "space-between",
          }}
        >
          <div>
            <h2 className="auth-title"> Thng tin c nhn</h2>
            <p className="auth-sub">Qun l thng tin c nhn v mt khu</p>
          </div>

          <div style={{ textAlign: "right" }}>
            <div
              style={{
                fontSize: 13,
                fontWeight: 700,
                textTransform: "capitalize",
                color: user.role === "admin" ? "#991B1B" : user.role === "moderator" ? "#f59e0b" : "#0f172a",
              }}
            >
              {user.role === "admin" && " Admin"}
              {user.role === "moderator" && " Moderator"}
              {user.role === "user" && " Ngi dng"}
            </div>
            {(user.role === "admin" || user.role === "moderator") && (
              <a
                href="/admin"
                style={{ fontSize: 12, color: "#4f46e5", display: "block" }}
              >
                i ti trang qun l
              </a>
            )}

            {/* Nt ng xut */}
            <button
              onClick={handleLogout}
              style={{
                marginTop: 6,
                background: "#e0233dff",
                color: "white",
                border: "none",
                padding: "6px 10px",
                borderRadius: "6px",
                cursor: "pointer",
                fontSize: 12,
              }}
            >
              ng xut
            </button>
          </div>
        </div>

        <AvatarUpload currentAvatar={user.avatar} onUploaded={fetchProfile} />

        <form onSubmit={handleUpdate}>
          <div className="form-group">
            <label>H v tn:</label>
            <input
              type="text"
              value={user.name}
              onChange={(e) => setUser({ ...user, name: e.target.value })}
              required
            />
          </div>

          <div className="form-group">
            <label>Email:</label>
            <input
              type="email"
              value={user.email}
              onChange={(e) => setUser({ ...user, email: e.target.value })}
              required
            />
          </div>

          <div className="form-group">
            <label>Mt khu mi (ty chn):</label>
            <input
              type="password"
              placeholder="Nhp mt khu mi nu mun i"
              value={newPassword}
              onChange={(e) => setNewPassword(e.target.value)}
            />
          </div>

          <div className="form-group">
            <label>Xc nhn mt khu mi</label>
            <input
              type="password"
              placeholder="Nhp li mt khu mi"
              value={confirmNewPassword}
              onChange={(e) => setConfirmNewPassword(e.target.value)}
            />
          </div>

          <div
            className="center"
            style={{
              display: "flex",
              gap: 12,
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <button className="btn" type="submit">
               Lu thay i
            </button>
            <button
              type="button"
              onClick={handleDeleteAccount}
              className="btn"
              style={{ background: "#ef4444", borderColor: "#ef4444" }}
            >
               Xa ti khon
            </button>
          </div>
        </form>

        {message && <p className="status-message">{message}</p>}
      </div>
    </div>
  );
}

export default ProfilePage;