import React, { useState, useEffect } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import api from "../../services/api";
import { useToast } from "../../contexts/ToastContext";
import "../../App.css";

export default function ResetPasswordForm() {
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const { addToast } = useToast();

  const [token, setToken] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);

  useEffect(() => {
    // Ly token t URL query params
    const tokenFromUrl = searchParams.get("token");
    if (!tokenFromUrl) {
      addToast("Link t li mt khu khng hp l", "error");
      navigate("/login");
    } else {
      setToken(tokenFromUrl);
    }
  }, [searchParams, navigate, addToast]);

  const handleSubmit = async (e) => {
    e.preventDefault();

    // Validation
    if (!password || !confirmPassword) {
      addToast("Vui lng nhp y  thng tin", "warning");
      return;
    }

    if (password.length < 6) {
      addToast("Mt khu phi c t nht 6 k t", "error");
      return;
    }

    if (password !== confirmPassword) {
      addToast("Mt khu xc nhn khng khp", "error");
      return;
    }

    setLoading(true);
    try {
      const response = await api.post("/auth/reset-password", {
        token,
        password,
      });

      console.log("Reset password response:", response.data);

      setSuccess(true);
      addToast(" " + (response.data.message || "i mt khu thnh cng!"), "success");

      // Redirect to login after 2 seconds
      setTimeout(() => {
        navigate("/login");
      }, 2000);
    } catch (err) {
      console.error("Reset password error:", err);
      addToast(
        err.response?.data?.message || "Li khi t li mt khu",
        "error"
      );
    } finally {
      setLoading(false);
    }
  };

  if (success) {
    return (
      <div style={styles.container}>
        <div style={styles.card}>
          <div style={styles.successIcon}></div>
          <h2 style={styles.title}>Thnh Cng!</h2>
          <p style={styles.description}>
            Mt khu ca bn  c t li thnh cng.
          </p>
          <p style={styles.hint}>ang chuyn n trang ng nhp...</p>
          <a href="/login" className="btn" style={{ display: "block", textAlign: "center" }}>
            ng nhp ngay
          </a>
        </div>
      </div>
    );
  }

  return (
    <div style={styles.container}>
      <div style={styles.card}>
        <div style={styles.iconWrapper}>
          <div style={styles.icon}></div>
        </div>

        <h2 style={styles.title}>t Li Mt Khu</h2>
        <p style={styles.description}>Nhp mt khu mi ca bn bn di.</p>

        <form onSubmit={handleSubmit} style={styles.form}>
          <div style={styles.inputGroup}>
            <label style={styles.label}>Mt khu mi</label>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder="Nhp mt khu mi (ti thiu 6 k t)"
              disabled={loading}
              style={styles.input}
              required
              minLength={6}
            />
            <div style={styles.passwordHint}>
              {password.length > 0 && password.length < 6 && (
                <span style={{ color: "#ef4444" }}>
                   Mt khu qu ngn (ti thiu 6 k t)
                </span>
              )}
              {password.length >= 6 && (
                <span style={{ color: "#10b981" }}>  di hp l</span>
              )}
            </div>
          </div>

          <div style={styles.inputGroup}>
            <label style={styles.label}>Xc nhn mt khu</label>
            <input
              type="password"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              placeholder="Nhp li mt khu mi"
              disabled={loading}
              style={styles.input}
              required
            />
            <div style={styles.passwordHint}>
              {confirmPassword.length > 0 &&
                password !== confirmPassword && (
                  <span style={{ color: "#ef4444" }}>
                     Mt khu khng khp
                  </span>
                )}
              {confirmPassword.length > 0 &&
                password === confirmPassword && (
                  <span style={{ color: "#10b981" }}> Mt khu khp</span>
                )}
            </div>
          </div>

          <button
            type="submit"
            className="btn"
            disabled={loading || password !== confirmPassword || password.length < 6}
            style={{ width: "100%", marginTop: 8 }}
          >
            {loading ? " ang x l..." : " t Li Mt Khu"}
          </button>
        </form>

        <div style={styles.footer}>
          <a href="/login" style={styles.link}>
             Quay li ng nhp
          </a>
        </div>
      </div>
    </div>
  );
}

const styles = {
  container: {
    minHeight: "100vh",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    background: "linear-gradient(135deg, #667eea 0%, #764ba2 100%)",
    padding: "20px",
  },
  card: {
    background: "white",
    borderRadius: "12px",
    boxShadow: "0 10px 40px rgba(0,0,0,0.2)",
    padding: "40px",
    maxWidth: "450px",
    width: "100%",
  },
  iconWrapper: {
    textAlign: "center",
    marginBottom: "20px",
  },
  icon: {
    fontSize: "64px",
    display: "inline-block",
  },
  successIcon: {
    fontSize: "64px",
    textAlign: "center",
    marginBottom: "20px",
  },
  title: {
    fontSize: "28px",
    fontWeight: "bold",
    color: "#1f2937",
    textAlign: "center",
    marginBottom: "12px",
  },
  description: {
    fontSize: "15px",
    color: "#6b7280",
    textAlign: "center",
    marginBottom: "24px",
    lineHeight: 1.6,
  },
  hint: {
    fontSize: "14px",
    color: "#9ca3af",
    textAlign: "center",
    marginBottom: "24px",
  },
  form: {
    marginTop: "24px",
  },
  inputGroup: {
    marginBottom: "16px",
  },
  label: {
    display: "block",
    fontSize: "14px",
    fontWeight: "600",
    color: "#374151",
    marginBottom: "8px",
  },
  input: {
    width: "100%",
    padding: "12px 16px",
    border: "2px solid #e5e7eb",
    borderRadius: "8px",
    fontSize: "15px",
    transition: "all 0.3s",
    boxSizing: "border-box",
  },
  passwordHint: {
    marginTop: "6px",
    fontSize: "13px",
    minHeight: "20px",
  },
  footer: {
    marginTop: "24px",
    textAlign: "center",
  },
  link: {
    color: "#667eea",
    textDecoration: "none",
    fontSize: "14px",
    fontWeight: "500",
  },
};
