import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import api from "../../services/api";
import { useSelector, useDispatch } from "react-redux";
import { selectUser } from "../../store/slices/authSlice";
import { logout as logoutThunk } from "../../store/slices/authSlice";
import { useToast } from "../../contexts/ToastContext";

export default function AdminUserList() {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const me = useSelector(selectUser);
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [editingId, setEditingId] = useState(null);
  const [editData, setEditData] = useState({ name: "", email: "", role: "" });
  const [query, setQuery] = useState("");
  const { addToast } = useToast();

  //  Lc & sp xp (admin ln u)
  const filtered = users
    .filter((u) => {
      if (!query) return true;
      const q = query.toLowerCase();
      return (
        (u.name || "").toLowerCase().includes(q) ||
        (u.email || "").toLowerCase().includes(q) ||
        (u.role || "").toLowerCase().includes(q)
      );
    })
    .sort((a, b) => {
      // Admin ln trc, moderator th 2, user cui (khng phn bit hoa/thng)
      const roleOrder = { admin: 0, moderator: 1, user: 2 };
      const ar = (a.role || "").toLowerCase();
      const br = (b.role || "").toLowerCase();
      const diff = (roleOrder[ar] ?? 3) - (roleOrder[br] ?? 3);
      if (diff !== 0) return diff;
      // ph: sp xp theo tn  n nh
      return (a.name || "").localeCompare(b.name || "");
    });

  const fetchUsers = async () => {
    try {
      const res = await api.get("/users");
      setUsers(res.data);
    } catch (err) {
      console.error("Failed to fetch users:", err);
      addToast(err.response?.data?.message || "Khng th ti danh sch users", "error");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchUsers();
  }, []);

  const handleDelete = async (id) => {
    if (!window.confirm("Bn c chc mun xa user ny?")) return;

    try {
      await api.delete(`/users/${id}`);
      setUsers((s) => s.filter((u) => u._id !== id));
      addToast(" xa user", "success");
    } catch (err) {
      console.error(err);
      addToast(err.response?.data?.message || "Xa tht bi", "error");
    }
  };

  const startEdit = (u) => {
    setEditingId(u._id);
    setEditData({ name: u.name, email: u.email, role: u.role });
  };

  const cancelEdit = () => {
    setEditingId(null);
    setEditData({ name: "", email: "", role: "" });
  };

  const saveEdit = async (id) => {
    try {
      // Ly user hin ti  so snh
      const currentUser = users.find(u => u._id === id);
      if (!currentUser) {
        addToast("Khng tm thy user  cp nht", "error");
        return;
      }

      // Kim tra xem c thay i g khng
      const nameChanged = editData.name !== currentUser.name;
      const emailChanged = editData.email !== currentUser.email;
      const roleChanged = me?.role === "admin" && editData.role !== currentUser.role;
      
      const hasChanges = nameChanged || emailChanged || roleChanged;
      
      if (!hasChanges) {
        addToast("Khng c thay i no  lu", "info");
        cancelEdit();
        return;
      }

      // Moderator khng c gi field role
      const payload = { name: editData.name, email: editData.email };
      if (me?.role === "admin") {
        payload.role = editData.role;
      }
      
      const res = await api.put(`/users/${id}`, payload);
      setUsers((s) => s.map((u) => (u._id === id ? res.data.user : u)));
      cancelEdit();
      
      // Thng bo chi tit v nhng g  thay i
      const changedFields = [];
      if (nameChanged) changedFields.push("tn");
      if (emailChanged) changedFields.push("email");
      if (roleChanged) changedFields.push("quyn");
      
      const message = changedFields.length > 0
        ? ` Cp nht ${changedFields.join(", ")} ca user thnh cng!`
        : " Cp nht user thnh cng!";
      
      addToast(message, "success");
    } catch (err) {
      console.error(err);
      addToast(err.response?.data?.message || "Cp nht tht bi", "error");
    }
  };

  const handleLogout = () => {
    dispatch(logoutThunk());
    addToast(" ng xut", "success");
    window.location.href = "/login";
  };

  //  Badge role
  const getRoleBadge = (role) => {
    const badges = {
      admin: { emoji: "", color: "#dc2626" },
      moderator: { emoji: "", color: "#f59e0b" },
      user: { emoji: "", color: "#6b7280" },
    };
    const badge = badges[role] || badges.user;
    return (
      <span
        style={{
          marginLeft: 6,
          color: badge.color,
          fontSize: 12,
          fontWeight: 600,
        }}
      >
        {badge.emoji}
      </span>
    );
  };

  return (
    <div className="app-container">
      {/* --- Header --- */}
      <div className="site-header">
        <h1 className="site-title">
          {me?.role === "admin" ? " Trang qun tr" : " Trang qun l"}
        </h1>
        <div style={{ textAlign: "right" }}>
          <div style={{ fontSize: 14, fontWeight: 700 }}>
            {me?.name || "User"}
          </div>
          <div style={{ fontSize: 12, color: "#6b7280" }}>{me?.email}</div>
          <div style={{ fontSize: 12, color: "#9ca3af", textTransform: "capitalize" }}>
            {me?.role}
          </div>
          <div
            style={{
              marginTop: 8,
              display: "flex",
              gap: 8,
              justifyContent: "flex-end",
            }}
          >
            {/* Nt xem profile */}
            <button
              className="btn secondary"
              onClick={() => navigate("/profile")}
              style={{ padding: "6px 10px" }}
            >
               Xem Profile
            </button>

            {/* Ch admin mi thy nt thm user */}
            {me?.role === "admin" && (
              <>
                <a
                  href="/admin/add"
                  className="btn secondary"
                  style={{ textDecoration: "none", padding: "6px 10px" }}
                >
                   Thm user
                </a>
                <button
                  className="btn secondary"
                  onClick={() => navigate("/admin/logs")}
                  style={{ padding: "6px 10px" }}
                >
                   Xem Logs
                </button>
              </>
            )}

            {/* Nt ng xut */}
            <button
              className="btn danger"
              onClick={handleLogout}
              style={{
                padding: "6px 10px",
                backgroundColor: "#dc2626",
                color: "white",
                border: "none",
                borderRadius: 6,
                cursor: "pointer",
              }}
            >
              ng xut
            </button>
          </div>
        </div>
      </div>

      {/* --- Ni dung bng --- */}
      <div className="card">
        <div className="admin-toolbar">
          <h3>Danh sch ngi dng ({users.length})</h3>
          <div className="admin-actions">
            <input
              className="search-input"
              placeholder="Tm theo tn, email, hoc role"
              value={query}
              onChange={(e) => setQuery(e.target.value)}
            />
          </div>
        </div>

        {loading ? (
          <p className="muted">ang ti...</p>
        ) : filtered.length === 0 ? (
          <p className="muted">Khng tm thy user no</p>
        ) : (
          <div className="table-responsive">
            <table className="user-table">
              <thead>
                <tr>
                  <th style={{ width: 220 }}>ID</th>
                  <th>Tn</th>
                  <th>Email</th>
                  <th>Role</th>
                  <th style={{ width: 220 }}>Hnh ng</th>
                </tr>
              </thead>
              <tbody>
                {filtered.map((u) => (
                  <tr key={u._id}>
                    <td style={{ wordBreak: "break-all", fontSize: 12 }}>
                      {u._id}
                    </td>

                    {/* --- Ct tn --- */}
                    <td>
                      {editingId === u._id ? (
                        <input
                          type="text"
                          value={editData.name}
                          onChange={(e) =>
                            setEditData({ ...editData, name: e.target.value })
                          }
                          style={{ width: "100%" }}
                        />
                      ) : (
                        <div style={{ display: "flex", alignItems: "center" }}>
                          <div className="avatar">
                            {(u.name || "U")
                              .split(" ")
                              .map((s) => s[0])
                              .slice(0, 2)
                              .join("")
                              .toUpperCase()}
                          </div>
                          <div>
                            <div style={{ fontWeight: 700 }}>
                              {u.name}
                              {getRoleBadge(u.role)}
                            </div>
                          </div>
                        </div>
                      )}
                    </td>

                    {/* --- Ct email --- */}
                    <td>
                      {editingId === u._id ? (
                        <input
                          type="email"
                          value={editData.email}
                          onChange={(e) =>
                            setEditData({ ...editData, email: e.target.value })
                          }
                          style={{ width: "100%" }}
                        />
                      ) : (
                        u.email
                      )}
                    </td>

                    {/* --- Ct role --- */}
                    <td>
                      {editingId === u._id && me?.role === "admin" ? (
                        <select
                          value={editData.role}
                          onChange={(e) =>
                            setEditData({ ...editData, role: e.target.value })
                          }
                          style={{ width: "100%", padding: "4px" }}
                        >
                          <option value="user">User</option>
                          <option value="moderator">Moderator</option>
                          <option value="admin">Admin</option>
                        </select>
                      ) : (
                        <span
                          style={{
                            textTransform: "capitalize",
                            fontWeight: 600,
                            color:
                              u.role === "admin"
                                ? "#dc2626"
                                : u.role === "moderator"
                                ? "#f59e0b"
                                : "#6b7280",
                          }}
                        >
                          {u.role}
                        </span>
                      )}
                    </td>

                    {/* --- Ct hnh ng --- */}
                    <td>
                      {editingId === u._id ? (
                        <>
                          <button
                            className="action-btn edit small"
                            onClick={() => saveEdit(u._id)}
                            style={{ marginRight: 8 }}
                          >
                            Lu
                          </button>
                          <button
                            className="action-btn small"
                            onClick={cancelEdit}
                          >
                            Hy
                          </button>
                        </>
                      ) : (
                        <>
                          <button
                            className="action-btn edit small"
                            onClick={() => startEdit(u)}
                            style={{ marginRight: 8 }}
                          >
                            Sa
                          </button>
                          {/* Ch admin mi thy nt xa */}
                          {me?.role === "admin" && (
                            <button
                              className="action-btn delete small"
                              onClick={() => handleDelete(u._id)}
                            >
                              Xa
                            </button>
                          )}
                        </>
                      )}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>
    </div>
  );
}
