import React from "react";
import { Navigate } from "react-router-dom";
import { useSelector } from "react-redux";
import { selectIsAuthenticated, selectUser } from "../../store/slices/authSlice";

/**
 * Protected Route Component using Redux
 * Chn truy cp nu cha ng nhp hoc khng  quyn
 */
export default function ProtectedRouteRedux({ 
  children, 
  requiredRole,
  requiredRoles 
}) {
  const isAuthenticated = useSelector(selectIsAuthenticated);
  const user = useSelector(selectUser);

  // Cha ng nhp  Redirect to login
  if (!isAuthenticated) {
    return <Navigate to="/login" replace />;
  }

  // Kim tra quyn (single role)
  if (requiredRole && user?.role !== requiredRole) {
    return (
      <div style={{ padding: "20px", textAlign: "center" }}>
        <h2> Khng c quyn truy cp</h2>
        <p>Bn cn quyn <strong>{requiredRole}</strong>  xem trang ny.</p>
        <a href="/profile" style={{ color: "#4f46e5" }}>Quay li trang c nhn</a>
      </div>
    );
  }

  // Kim tra quyn (multiple roles)
  if (requiredRoles && !requiredRoles.includes(user?.role)) {
    return (
      <div style={{ padding: "20px", textAlign: "center" }}>
        <h2> Khng c quyn truy cp</h2>
        <p>Bn cn mt trong cc quyn sau: <strong>{requiredRoles.join(", ")}</strong></p>
        <a href="/profile" style={{ color: "#4f46e5" }}>Quay li trang c nhn</a>
      </div>
    );
  }

  return children;
}

